import React, { useState, useEffect } from "react";
import "./OverviewStats.scss";
import TestCaseCheckbox from "../../testcaseCheckbox/TestCaseCheckbox";

import ReportDropDown from "../../dropdownreport/ReportDropDown";

import MultiLineChart from "../../multilinechart/MultiLineChart";

function OverviewStats() {
  const data = [
    { build: "HR MR2.8.1 A12 - bc5", avgLoadTime: 2.3 },
    { build: "HR MR2.8.1 A12 - ab2", avgLoadTime: 2.5 },
    { build: "HR MR2.8 A12 - bc5", avgLoadTime: 2.4 },
    { build: "HR MR2.8 A12 - ab2", avgLoadTime: 2.6 },
    { build: "HR MR2.5 A10 - bc5", avgLoadTime: 3.0 },
    { build: "AT ONSITE MR4.6 A12 - 46d", avgLoadTime: 1.8 },
    { build: "AT ONSITE MR4.5 A12 - 46d", avgLoadTime: 1.9 },
    { build: "AT ONSITE MR4.4 A12 - 46d", avgLoadTime: 2.0 },
    { build: "AT ONSITE MR2.8.1 A12 - 46d", avgLoadTime: 2.1 },
    { build: "AT MR4.6 A12 -757", avgLoadTime: 1.7 },
    { build: "AT MR4.5 A12 -757", avgLoadTime: 1.6 },
    { build: "AT MR4.4 A12 -757", avgLoadTime: 1.5 },
    { build: "AT MR2.8.1 A12 - d5c", avgLoadTime: 2.2 },
    { build: "AT MR2.7.02 A12 - d5c", avgLoadTime: 2.8 },
    { build: "AT MR2.6 A12 - 914", avgLoadTime: 2.9 },
    { build: "AT MR2.6 A10 -757", avgLoadTime: 3.1 },
    { build: "HU SDMC MR2.9 A10 - b3e", avgLoadTime: 1.4 },
    { build: "HU ONSITE SDMC MR4.5 A12 - b3a", avgLoadTime: 1.3 },
    { build: "HU ONSITE SDMC MR2.9.13 A12 - b3a", avgLoadTime: 1.2 },
    { build: "HU SEI MR2.9 A12 - ab2", avgLoadTime: 2.7 },
    { build: "HU SEI MR2.9.13 A12 - d2e", avgLoadTime: 2.9 },
    { build: "HU SEI MR4.5 A12 - d2e", avgLoadTime: 3.2 },
    { build: "PL MR4.4 A10 - b3e", avgLoadTime: 1.0 },
    { build: "PL MR4.5 A10 - b3e", avgLoadTime: 1.1 },
    { build: "PL MR2.6 A10 - 8d8", avgLoadTime: 3.3 },
    { build: "PL MR4.4 A10 - ab2", avgLoadTime: 3.4 }
  ];
  const handleSelect = (option) => {
    console.log(`Selected: ${option}`);
    setSelectedOption(option);
  };

  const handleSelectOne = (option) => {
    setSelectedOptionOne(option);
  };
  const [selectedOption, setSelectedOption] = useState("Load Time");
  const [selectedTestCases, setSelectedTestCases] = useState([]);
  const [selectOptionOne, setSelectedOptionOne] = useState("ALL");

  const handleSelectedTestCasesChange = (selectedTestCases) => {
    setSelectedTestCases(selectedTestCases);
  };
  const [dataList, setDataList] = useState(data);

  useEffect(() => {
    if (selectOptionOne === "ALL") {
      setDataList(data);
    } else {
      const filtered = data.filter((d) => d.build.startsWith(selectOptionOne));
      console.log(filtered);
      setDataList(filtered);
    }
  }, [selectOptionOne]);

  const renderChart = () => {
    switch (selectedOption) {
      case "Load Time":
        return <MultiLineChart dataList={dataList} list={selectedTestCases} />;
      case "CPU Usage":
        return <MultiLineChart dataList={dataList} list={selectedTestCases} />;
      case "RAM Usage":
        return <MultiLineChart dataList={dataList} list={selectedTestCases} />;
      default:
        return null;
    }
  };

  return (
    <div className="overview-stats">
      <div className="inner-overview-stats">
        <div className="list-and-chart">
          <div className="test-list">
            <TestCaseCheckbox onSelectedTestCasesChange={handleSelectedTestCasesChange} />
          </div>
          <div className="linechart-section">
            {/* <LineChart /> */}
            <div className="heading-dropdown">
              <h4>Chart</h4>
              <div className="drop-downs">
                <ReportDropDown
                  options={["ALL", "AT", "PL", "HU", "HR", "ME", "MKT"]}
                  onSelect={handleSelectOne}
                  option={selectOptionOne}
                />
                <ReportDropDown
                  options={["Load Time", "CPU Usage", "RAM Usage"]}
                  onSelect={handleSelect}
                  option={selectedOption}
                />
              </div>
            </div>
            <div className="charts-section-stats">
              {/* <MultiLineChart list={selectedTestCases} /> */}
              {renderChart()}
            </div>
          </div>
        </div>

        <div className="table-info">
          <div className="inner-table-box">
            <div className="table-heading">
              <h4>Tables</h4>
            </div>
            <div className="table-content">
              <div className="column-info"></div>
              <div className="table-row"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default OverviewStats;
